._mixN6 {
  font-size: 0;
  position: relative;
  margin: 0; }
  ._mixN6 video {
    width: 75%;
    height: auto;
    display: inline-block;
    background-color: #faf6f4; }

._mixN6 figcaption {
  position: absolute;
  right: 0;
  top: 0;
  background: #333333;
  width: 25%;
  font-size: 0.8rem;
  color: #ffffff99;
  height: 100%;
  overflow-y: auto; }

._mixN6 figcaption ol {
  position: relative;
  list-style-type: none;
  margin: 0;
  padding: 0; }

._mixN6 figcaption ol li a {
  display: block;
  padding: 0.7rem 1rem;
  border-bottom: 1px dashed #000;
  transition: 0.3s; }

._TK-Q2 {
  background: #ffffff;
  opacity: 0.6;
  color: #000; }

._mixN6 figcaption ol li a:not(._TK-Q2):hover,
._mixN6 figcaption ol li a:not(._TK-Q2):focus {
  background: #666;
  color: #000;
  cursor: pointer; }

._mixN6 figcaption::-webkit-scrollbar {
  width: 13px;
  height: 100%; }

._mixN6 figcaption::-webkit-scrollbar-track {
  background-color: #000; }

._mixN6 figcaption::-webkit-scrollbar-thumb:vertical {
  background: #666;
  width: 13px;
  height: 20px; }

._mixN6 ul {
  margin-left: 17px;
  height: 0px;
  position: relative;
  transition: visibility 2s, opacity 2s linear; }
  ._mixN6 ul li {
    background-color: #000000;
    bottom: 0;
    left: 0;
    height: 6px;
    position: absolute;
    width: 8px;
    cursor: pointer; }

@media screen and (max-width: 500px) {
  ._mixN6 video,
  ._mixN6 figcaption {
    display: block;
    width: 100%; }
  ._mixN6 video {
    border-bottom: 10px solid #111; }
  ._mixN6 figcaption {
    position: static;
    height: auto;
    max-height: 200px; } }
